
const InfermiereGeneratorClass = require('./infermiere/Generator')
const MedicoGeneratorClass = require('./medico/Generator')
const PsicologoGeneratorClass = require('./psicologo/Generator')
const SocialeGeneratorClass = require('./sociale/Generator')

const data = require('../assets/conditions.json')

const reset = () => {
    data.patient.gender.value = null
    for (const item in data.presidi) {
        data.presidi[item].value = false
    }
    for (const item in data.scale) {
        data.scale[item].value = false
    }
    for (const item in data.diagnosi) {
        data.diagnosi[item].value = false
    }
    for (const item in data.sintomi) {
        data.sintomi[item].value = false
    }
    for (const item in data.psicosociale) {
        data.psicosociale[item].value = false
    }
}

module.exports = function () {

    const module = {}

    module.data = data

    module.reset = reset

    module.infermiere = {
        respirare: new InfermiereGeneratorClass('respirare'),
        alimentazione: new InfermiereGeneratorClass('alimentazione'),
        eliminazione: new InfermiereGeneratorClass('eliminazione'),
        igiene: new InfermiereGeneratorClass('igiene'),
        movimento: new InfermiereGeneratorClass('movimento'),
        riposo: new InfermiereGeneratorClass('riposo'),
        circolatorio: new InfermiereGeneratorClass('circolatorio'),
        sicurezza: new InfermiereGeneratorClass('sicurezza'),
        comunicazione: new InfermiereGeneratorClass('comunicazione'),
        terapia: new InfermiereGeneratorClass('terapia'),
        diagnostica: new InfermiereGeneratorClass('diagnostica')
    }

    module.medico = {
        respirare: new MedicoGeneratorClass('respirare'),
        alimentazione: new MedicoGeneratorClass('alimentazione'),
        eliminazione: new MedicoGeneratorClass('eliminazione'),
        igiene: new MedicoGeneratorClass('igiene'),
        movimento: new MedicoGeneratorClass('movimento'),
        riposo: new MedicoGeneratorClass('riposo'),
        circolatorio: new MedicoGeneratorClass('circolatorio'),
        sicurezza: new MedicoGeneratorClass('sicurezza'),
        comunicazione: new MedicoGeneratorClass('comunicazione'),
        terapia: new MedicoGeneratorClass('terapia'),
        diagnostica: new MedicoGeneratorClass('diagnostica')
    }

    module.psicologo = {
        comunicazione: new PsicologoGeneratorClass('comunicazione')
    }

    module.sociale = {
        sociale: new SocialeGeneratorClass('sociale'),
    }

    return module
}
