

<template>
  <div>
    <v-card v-if="cols == 4" elevation="2">
      <table style="width:100%;">
        <tr>
          <td class="td-1 blocks-4">
            <NeedsActive :needs="needs" :pai="pai_text"></NeedsActive>
          </td>
          <td class="td-2 blocks-4">
            <GeneratedProblems :needs="needs" @update="onUpdateProblems"></GeneratedProblems>
          </td>
          <td class="td-3 blocks-4">
            <GeneratedObjectives :needs="needs" @update="onUpdateObjectives"></GeneratedObjectives>
          </td>
          <td class="td-4 blocks-4">
            <GeneratedStrategies :needs="needs" @update="onUpdateStrategies"></GeneratedStrategies>
          </td>
        </tr>
      </table>
    </v-card>
    <!-- TABLE -->
    <v-card v-else-if="cols == 2" elevation="16">
      <table style="width:100%;">
        <tr style="height: 100%;">
          <td class="td-1 blocks-2">
            <NeedsActive :needs="needs" :pai="pai_text"></NeedsActive>
          </td>
          <td class="td-2 blocks-2">
            <GeneratedProblems :needs="needs" @update="onUpdateProblems"></GeneratedProblems>
          </td>
        </tr>
        <tr>
          <td class="td-3 blocks-2">
            <GeneratedObjectives :needs="needs" @update="onUpdateObjectives"></GeneratedObjectives>
          </td>
          <td class="td-4 blocks-2">
            <GeneratedStrategies :needs="needs" @update="onUpdateStrategies"></GeneratedStrategies>
          </td>
        </tr>
      </table>
    </v-card>
    <!-- MOBILE -->
    <v-card v-else elevation="12">
      <table style="width:100%;">
        <tr>
          <td class="td-1 blocks-1">
            <NeedsActive :needs="needs" :pai="pai_text"></NeedsActive>
          </td>
        </tr>
        <tr>
          <td class="td-2 blocks-1">
            <GeneratedProblems :needs="needs" @update="onUpdateProblems"></GeneratedProblems>
          </td>
        </tr>
        <tr>
          <td class="td-3 blocks-1">
            <GeneratedObjectives :needs="needs" @update="onUpdateObjectives"></GeneratedObjectives>
          </td>
        </tr>
        <tr>
          <td class="td-4 blocks-1">
            <GeneratedStrategies :needs="needs" @update="onUpdateStrategies"></GeneratedStrategies>
          </td>
        </tr>
      </table>
    </v-card>
  </div>
</template>

<script>
import NeedsActive from './NeedsActive.vue'
import GeneratedProblems from './GeneratedProblems.vue'
import GeneratedObjectives from './GeneratedObjectives.vue'
import GeneratedStrategies from './GeneratedStrategies.vue'
export default {
  name: 'GeneratedPai',
  components: {
    NeedsActive,
    GeneratedProblems,
    GeneratedObjectives,
    GeneratedStrategies
  },
  computed: {
    isMobile() { return this.$store.getters.isMobile },
    window_width() { return this.$store.getters.window_width },
  },
  data: () => ({
    cols: 1,
    pai_text: '',
    problems_text: '',
    objectives_text: '',
    strategies_text: '',
    pai_data: null,
    // needs
    needs: {
      respirare: null,
      alimentazione: null,
      eliminazione: null,
      igiene: null,
      movimento: null,
      riposo: null,
      circolatorio: null,
      sicurezza: null,
      comunicazione: null,
      terapia: null
    }
  }),
  watch: {
    window_width: function () {
      this.setListColumns()
    },
    pai_data: {
      deep: true,
      handler: function () {
        this.writePai()
      }
    },
  },
  beforeMount() {
    this.pai_data = this.$pai.data
  },
  mounted() {
    this.setListColumns()
    this.writePai()
  },
  methods: {
    writePai() {
      this.needs.respirare = this.$pai.medico.respirare.getResults(this.pai_data)
      this.needs.alimentazione = this.$pai.medico.alimentazione.getResults(this.pai_data)
      this.needs.eliminazione = this.$pai.medico.eliminazione.getResults(this.pai_data)
      this.needs.igiene = this.$pai.medico.igiene.getResults(this.pai_data)
      this.needs.movimento = this.$pai.medico.movimento.getResults(this.pai_data)
      this.needs.riposo = this.$pai.medico.riposo.getResults(this.pai_data)
      this.needs.circolatorio = this.$pai.medico.circolatorio.getResults(this.pai_data)
      this.needs.sicurezza = this.$pai.medico.sicurezza.getResults(this.pai_data)
      this.needs.comunicazione = this.$pai.medico.comunicazione.getResults(this.pai_data)
      this.needs.terapia = this.$pai.medico.terapia.getResults(this.pai_data)
    },
    writePaiText() {
      this.pai_text = 'PROBLEMI\n' + this.problems_text + '\n\n'
      this.pai_text += 'OBIETTIVI\n' + this.objectives_text + '\n\n'
      this.pai_text += 'STRATEGIE\n' + this.strategies_text + '\n\n'
    },
    onUpdateProblems(text) {
      this.problems_text = text
      this.writePaiText()
    },
    onUpdateObjectives(text) {
      this.objectives_text = text
      this.writePaiText()
    },
    onUpdateStrategies(text) {
      this.strategies_text = text
      this.writePaiText()
    },
    setListColumns() {
      const width = this.window_width
      if (width >= 1600) this.cols = 4
      else if (width < 1600 && width >= 800) this.cols = 2
      else this.cols = 1
    }
  },
}
</script>

