<template>
    <div v-if="needs">
        <v-card-title style="margin: 5px">
            BISOGNI ATTIVI
            <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon color="green" style="margin-left: 10px;" v-bind="attrs"
                        v-on="on">mdi-information-outline</v-icon>
                </template>
                <v-card width="400">
                    <v-card-title>Informazioni sui bisogni</v-card-title>
                    <v-card-text>
                        <!-- <div v-html="html"></div> -->
                    </v-card-text>
                </v-card>
            </v-menu>
        </v-card-title>
        <v-card-text>
            <!-- <div class="bisogno" :class="{ active: needs.respirare }">Bisogno di respirare</div>
            <div class="bisogno" :class="{ active: needs.alimentazione }">Bisogno di alimentazione e idratazione</div>
            <div class="bisogno" :class="{ active: needs.eliminazione }">Bisogno di eliminazione urinaria e intestinale
            </div>
            <div class="bisogno" :class="{ active: needs.igiene }">Bisogno di igiene</div>
            <div class="bisogno" :class="{ active: needs.movimento }">Bisogno di movimento</div>
            <div class="bisogno" :class="{ active: needs.riposo }">Bisogno di riposo e sonno</div>
            <div class="bisogno" :class="{ active: needs.circolatorio }">Bisogno di funzione cardiocircolatorio</div>
            <div class="bisogno" :class="{ active: needs.sicurezza }">Bisogno di ambiente sicuro</div>
            <div class="bisogno" :class="{ active: needs.comunicazione }">Bisogno di interazione nella comunicazione</div>
