            <div class="bisogno" :class="{ active: needs.diagnostica }">Bisogno di procedure diagnostiche</div> -->

            <v-btn @click="copyContent" :color="copy_success ? 'green' : 'black'" outlined small
                style="margin:10px 0 0 0;width:100%">{{ !copy_success ? 'COPIA INTERO PAI' : 'COPIATO!' }}</v-btn>
        </v-card-text>
    </div>
</template>

<script>
export default {
    name: 'NeedsActive',
    props: {
        needs: Object,
        pai: String
    },
    data: () => ({
        html: null,
        copy_success: false
    }),
    mounted() {
        this.html = `
                Lo scopo della presa in carico  migliorare la qualit di vita dell'ospite 
                assistendolo nella progressiva perdita di autonomia 
                e nel manifestarsi di disturbi (sintomi), sia fisici che psichici,
                che possono causare problemi psicologici, sociali e spirituali.<br>
                La presa in carico comprende anche la famiglia, alla quale sar fornito tutto il necessario supporto per affrontare tale difficile momento.
            `
    },
    methods: {
        async copyContent() {
            try {
                if (!this.pai) return
                await navigator.clipboard.writeText(this.pai);
                this.copy_success = true
                setTimeout(() => {
                    this.copy_success = false
                }, 2000);
            } catch (err) {
                console.error('Failed to copy: ', err);
            }
        }
    }
}

</script>

