<template>
    <div v-if="needs">
        <v-card-title style="margin: 5px">
            BISOGNI ATTIVI
            <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon color="green" style="margin-left: 10px;" v-bind="attrs"
                        v-on="on">mdi-information-outline</v-icon>
                </template>
                <v-card width="400">
                    <v-card-title>Informazioni sui bisogni</v-card-title>
                    <v-card-text>
                        <!-- <div v-html="html"></div> -->
                    </v-card-text>
                </v-card>
            </v-menu>
        </v-card-title>
        <v-card-text>
            <!-- <div class="bisogno" :class="{ active: needs.sociale }">Bisogno sociale</div> -->
            <v-btn @click="copyContent" :color="copy_success ? 'green' : 'black'" outlined small
                style="margin:10px 0 0 0;width:100%">{{ !copy_success ? 'COPIA INTERO PAI' : 'COPIATO!' }}</v-btn>
        </v-card-text>
    </div>
</template>

<script>
export default {
    name: 'NeedsActive',
    props: {
        needs: Object,
        pai: String
    },
    data: () => ({
        html: null,
        copy_success: false
    }),
    mounted() {
        this.html = `
                Lo scopo della presa in carico  migliorare la qualit di vita dell'ospite.<br>
                Sono prese in considerazione le problematiche famigliari ed economiche.
            `
    },
    methods: {
        async copyContent() {
            try {
                if (!this.pai) return
                await navigator.clipboard.writeText(this.pai);
                this.copy_success = true
                setTimeout(() => {
                    this.copy_success = false
                }, 2000);
            } catch (err) {
                console.error('Failed to copy: ', err);
            }
        }
    }
}

</script>

<style scoped>
.bisogno {
    height: 50px;
    background-color: rgb(175, 175, 175);
    padding: 12px;
}

.active {
    background-color: green;
    color: white;
    font-weight: bols;
}
</style>